function btnToggleFavCocktailModal(btn, isChecked) {
    if (isChecked) {
        btn.classList.add("is-checked");

    } else {
        btn.classList.remove("is-checked");

    }
}

function btnToggleFavIngredientModal(btn, isChecked) {
    if (isChecked) {
        btn.classList.add("is-checked");

    } else {
        btn.classList.remove("is-checked");
    }
}
